import "./App.css";
import { Button, Navbar, Container, Nav } from "react-bootstrap";
import "bootstrap/dist/css/bootstrap.css";
import {
  useState,
  useEffect,
  createContext,
  Suspense,
  lazy,
  useTransition,
  useDeferredValue,
} from "react";
import data from "./data.js";
import { Routes, Route, Link, useNavigate, Outlet } from "react-router-dom";
// import ProductDetail from "./routes/ProductDetail";
// import Cart from "./routes/Cart";
import axios from "axios";
import { useQuery } from "react-query";

export let Context1 = createContext();

const ProductDetail = lazy(() => import("./routes/ProductDetail")); // 빌드 시 별도의 js 파일로 분리해서 첫 페이지 로딩속도 향상, 대신 컴포넌트 로딩시간 발생
const Cart = lazy(() => import("./routes/Cart"));

function App() {
  let [isPending, startTransition] = useTransition();
  /** 성능저하되는 state변경 startTransition 함수로 감싸
   * ex) <input onChange={(e)=>{
   *  startTransition(()=>{
   *    setName(e.target.value)   문제의 구간, 이 코드의 실행시점 늦춰줌
   * })}}/>
   * isPending은 startTransition이 처리중일때 true를 반환
   */
  // let state = useDeferredValue(state); // state안에 변동사항이 생기면 늦게 처리해줌

  useEffect(() => {
    if (!localStorage.getItem("watched"))
      localStorage.setItem("watched", JSON.stringify([]));
  }, []);

  let [shoes, setShoes] = useState(data);
  let [stock] = useState([10, 11, 12]);
  let [btnCnt, setBtnCnt] = useState(1);
  let [btnHidden, setBtnHidden] = useState(false);
  let [loading, setLoading] = useState(false);
  let navigate = useNavigate();

  useEffect(() => {
    if (btnCnt !== 1) {
      //useRef로 hook custom 해보기
      console.log(btnCnt);
      setLoading(true);
      axios
        .get("https://codingapple1.github.io/shop/data" + btnCnt + ".json")
        .then((rst) => {
          console.log(rst.data);
          console.log(shoes);
          let temp = [...shoes, ...rst.data];
          setShoes(temp);
        })
        .catch(() => {
          console.log("실패");
        })
        .finally(() => {
          setLoading(false);

          if (btnCnt === 3) {
            setBtnHidden(true);
          }
        });
    }
  }, [btnCnt]);

  let result = useQuery(
    // 같은 경로로 요청해도 하나만 실행? 다른쪽 결과 캐싱가능
    "userData",
    () =>
      // 실시간 거래 (refetch)
      axios.get("https://codingapple1.github.io/userdata.json").then((e) => {
        return e.data;
      }),
    { staleTime: 2000 } //refetch 2초안에
  );
  console.log(result);
  // result.data // 성공
  // result.isLoading  // 로딩중
  // result.error // 실패

  return (
    <div className="App">
      {/* <Button variant="primary">Primary</Button> */}
      <Navbar bg="light" data-bs-theme="light">
        <Container>
          <Navbar.Brand href="#home">ShoeShop</Navbar.Brand>
          <Nav className="me-auto">
            <Nav.Link
              onClick={() => {
                navigate(`${process.env.PUBLIC_URL}/`);
              }}
            >
              Home
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                navigate(`${process.env.PUBLIC_URL}/detail/0`);
              }}
            >
              detail
            </Nav.Link>
            <Nav.Link
              onClick={() => {
                navigate(`${process.env.PUBLIC_URL}/cart`);
              }}
            >
              cart
            </Nav.Link>
          </Nav>
          <Nav className="ms-auto">
            {result.isLoading && "로딩중"}
            {result.error && "에러"}
            {result.data && result.data.name}
          </Nav>
        </Container>
      </Navbar>

      {/* <Link to="/">홈</Link>
      <Link to="/detail">상세</Link> */}

      <Routes>
        <Route
          path="https://hkkim2019.github.io/shop/"
          element={
            <>
              <div
                className="main-bg"
                style={{
                  backgroundImage: `url(${process.env.PUBLIC_URL}/img/bg.png)`,
                }}
              ></div>
              {loading ? (
                <div className="alert alert-warning">로딩중</div>
              ) : null}
              <div className="container">
                <div className="row">
                  {shoes.map((e, i) => {
                    return <Product shoes={e} index={i} />;
                  })}
                </div>
              </div>
              {!btnHidden ? (
                <button
                  onClick={() => {
                    setBtnCnt(btnCnt + 1);
                  }}
                >
                  더보기
                </button>
              ) : null}
            </>
          }
        />
        <Route
          path="https://hkkim2019.github.io/shop/detail/:id"
          element={
            /*ContextAPI특징: state(ex: stock) 변경 시 자식까지 재렌더링 됨,
              컴포넌트 재사용 어려움*/
            /* Suspense: lazy로 인한 흰화면 노츨을 대체, 보통 전체에 쓰이니 Routes 를 감싸서 씀*/
            <Context1.Provider value={{ stock }}>
              <Suspense fallback={<div>로딩중</div>}>
                <ProductDetail shoes={shoes} />
              </Suspense>
            </Context1.Provider>
          }
        />
        <Route
          path="https://hkkim2019.github.io/shop/cart"
          element={<Cart />}
        />

        <Route
          path="https://hkkim2019.github.io/shop/about"
          element={<About />}
        >
          <Route path="member" element={<div>멤버</div>} />
          <Route path="location" element={<div>위치</div>} />
        </Route>
        {/* <Route path="/about/member" element={<About />} />
        <Route path="/about/location" element={<About />} /> */}
        <Route
          path="https://hkkim2019.github.io/shop/event"
          element={<Event />}
        >
          <Route path="one" element={<div>첫 주문시 양배추즙 서비스</div>} />
          <Route path="two" element={<div>생일기념 쿠폰받기</div>} />
        </Route>
        <Route path="*" element={<div>없는페이지</div>} />
      </Routes>
    </div>
  );
}

function Product(props) {
  return (
    <div className="col-md-4">
      <img
        // src={process.env.PUBLIC_URL + "/img/shoes" + props.index + ".jpg"}
        src={
          "https://codingapple1.github.io/shop/shoes" +
          (props.index + 1) +
          ".jpg"
        }
        width="80%"
      />
      <h4>{props.shoes.title}</h4>
      <p>{props.shoes.price}</p>
    </div>
  );
}

function About() {
  return (
    <div>
      <h4>회사정보</h4>
      <Outlet></Outlet>
    </div>
  );
}
function Event() {
  return (
    <div>
      <h4>오늘의 이벤트</h4>
      <Outlet></Outlet>
    </div>
  );
}

export default App;
