import { createSlice } from "@reduxjs/toolkit";

let user = createSlice({
  // 슬라이스 생성 , useState같은것
  name: "user",
  initialState: { name: "kim", age: 20 },
  reducers: {
    // state 수정하는 함수
    changeName(state) {
      //   return "hyo " + state;
      state.name = "hyo " + state.name;
    },
    plusAge(state) {
      state.age += 1;
    },
  },
});

export let { changeName, plusAge } = user.actions;
export default user;
